import React from "react";
import { connect } from "react-redux";
import * as Actions from "../../store/Candidate/CandidateAction";
import * as JDActions from "../../store/JobDescription/JobDescriptionAction";
import * as CActions from "../../store/Resumesource/resumesourceAction";
import * as ResponseActions from "../../store/Response/Response.Action";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/sortableTable";
import { Card, CardContent, IconButton } from "@material-ui/core";
import Delete from "../../assets/images/Icon material-delete.svg";
import "./Selected.css";
import SearchInput from "../../components/Atoms/CustomizedInputs/SearchInput";
import searchIcon from "../../assets/images/Icon feather-search.svg";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput"
import axios from "axios";
import serverAddress from "../../config";
import { debounce } from "lodash";
import Pagination from "../../components/Atoms/Pagination";

class Selected extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      job: "",
      jd_id: "",
      openAns: "",
      openRes: "",
      startDate: new Date(),
      endDate: new Date(),
      selected: true,
      value: "",
      jdvalue: "",
      rp: "",
      page: 1,
      limit: 10,
      options: [],
      id: "",
      searchItem: "",
      candidate_details: [],
    };
  }

  componentDidMount() {
    this.props.JDAction.getJobDescriptionData();
  }

  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.responseReducer;
    const cur = this.props && this.props.responseReducer;

    if (prev.isResponseUpdated !== cur.isResponseUpdated) {
      const payload = {
        id: this.state.optid,
      };
      this.props.ResponseActions.getResponseData(payload);
    }
  }

  handleOpenAnswers = () => {
    this.setState({ openAns: true });
  };

  handleOpenResume = () => {
    this.setState({ openRes: true });
  };

  handleClose = () => {
    this.setState({ openAns: false });
  };

  handleCloseResume = () => {
    this.setState({ openRes: false });
  };

  handleSelect = (e) => {
    this.setState({ job: e.label, jd_id: e.value });
    const jobid = e.value;
    const payload = {
      id: e.value,
    };
    this.getdata(payload);
  };

  handleSearch = (e) => {
    this.setState({ value: e.value, searchItem: e.target.value });
  };

  debounceLog = debounce(() => {
    this.setState({ showLoader: true });
    const payload = {
      name: this.state.searchItem,
      id: this.state.jd_id,
      isDeleted: false,
    };
    this.props.ResponseActions.getResponseData(payload);
  }, 700);

  handlesearchCandidate = (e) => {
    this.debounceLog();
  };

  handleStartDate = (date) => {
    this.setState({ startDate: date });
  };

  handleEndDate = (date) => {
    this.setState({ endDate: date });
  };

  handleDeleteCandidate = (val) => {
    const payload = {
      isDeleted: true,
      id: val.original.id,
    };
    this.props.ResponseActions.updateResponse(payload);
  };

  async getdata(payload) {
    this.props.ResponseActions.getResponseData(payload);
  }

  handleBackPagination = async (e) => {
    if (this.state.page != 1) {
      await this.setState({ page: this.state.page - 1 });
    }
  };
  handleNextPagination = async () => {
    if (this.state.page != this.state.totalPage) {
      await this.setState({ page: this.state.page + 1 });
    }
  };

  handlePagination = async (e) => {
    await this.setState({ page: parseInt(e.target.innerText) });
  };

  render() {
    let data = [];
    let searchData = [];
    let totalPage = 1;
    let dropDowndata = [];
    let optionsdata = [];
    if (
      this.props &&
      this.props.JobDescriptionReducer &&
      this.props.JobDescriptionReducer.getJobDescriptionData &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data
        .jobDescriptions
    ) {
      dropDowndata =
        this.props.JobDescriptionReducer.getJobDescriptionData.data
          .jobDescriptions;
      optionsdata = dropDowndata.map((d) => ({
        value: d._id,
        label:
          " " +
          (d.title && d.title.jobtitle) +
          " - " +
          d.minExperience +
          " to " +
          d.maxExperience +
          " Years of experience",
      }));
    }
    let data1 = [];
    if (
      this.props &&
      this.props.responseReducer &&
      this.props.responseReducer.getResponseData &&
      this.props.responseReducer.getResponseData.data &&
      this.props.responseReducer.getResponseData.data.response &&
      this.props.responseReducer.getResponseData.data.response.length
    ) {
      const mapped =
        this.props.responseReducer.getResponseData.data.response.length;
      totalPage =
        mapped % this.state.limit == 0
          ? parseInt(mapped / this.state.limit)
          : parseInt(mapped / this.state.limit + 1);
      this.props.responseReducer.getResponseData.data.response.map((item) => {
        if (item.status === "next round")
          data1.push({
            id: item._id,
            name: item.CandidateId.name,
            email: item.CandidateId.email,
            phone: item.CandidateId.phone,
          });
      });
    }
    if (this.state.page == 1) {
      data1 = data1.slice(0, 10);
    } else {
      let start = 10 * (this.state.page - 1);
      data1 = data1.slice(start, start + 10);
    }

    data = data1;

    if (
      this.props &&
      this.props.resumesourceReducer &&
      this.props.resumesourceReducer.getCandidateData &&
      this.props.resumesourceReducer.getCandidateData.data &&
      this.props.resumesourceReducer.getCandidateData.data.users
    ) {
      let data1 = [];
      const mapped =
        this.props.resumesourceReducer.getCandidateData.data.users.length;
      totalPage =
        mapped % this.state.limit == 0
          ? parseInt(mapped / this.state.limit)
          : parseInt(mapped / this.state.limit + 1);
      this.props.resumesourceReducer.getCandidateData.data.users.map((item) => {
        data1.push({
          id: item._id,
          name: item.name,
          email: item.email,
          phone: item.phone,
        });
      });
      if (this.state.page == 1) {
        data1 = data1.slice(0, 10);
      } else {
        let start = 10 * (this.state.page - 1);
        data1 = data1.slice(start, start + 10);
      }
      searchData = data1;
    }

    let inboundDatacolumn = [
      {
        Header: "Name",
        accessor: "name",
        minWidth: 200,
      },

      {
        Header: "Email",
        accessor: "email",
        minWidth: 220,
        style: { overflow: "visible" },
      },
      {
        Header: "Phone",
        accessor: "phone",
        minWidth: 200,
      },

      {
        Header: "",
        accessor: "icons",

        Cell: (val) => {
          return (
            <div>
              <IconButton className="select-iconBtn" endIcon={Delete}>
                <img
                  src={Delete}
                  onClick={() => this.handleDeleteCandidate(val)}
                />
              </IconButton>
            </div>
          );
        },
      },
    ];

    return (
      <div>
        <Card className="selectedCard1">
          <CardContent>
            <div className="selected-card1-wrap">
              <div className="select-drop">
                <SelectInput
                  className="react-select"
                  onChange={this.handleSelect}
                  options={optionsdata}
                  placeholder={
                    this.state.jdvalue
                      ? this.state.jdvalue
                      : "Select Job Description"
                  }
                />
              </div>
              <div className="select-searchInput">
                <SearchInput
                  className="selectSearchInput"
                  imageOnClick={() => {}}
                  placeholder="Search For Candidate Email"
                  value={this.state.value}
                  onChange={(e) => {
                    this.handleSearch(e);
                  }}
                />
                <img
                  src={searchIcon}
                  className="select-searchicon"
                  alt="SearchIcon"
                  onClick={() =>
                    this.handlesearchCandidate(this.state.searchItem)
                  }
                />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="selectedCard2">
          {this.state.selected ? (
            <div className="select-table">
              <TableComponent
                className={"selected-table"}
                data={this.state.searchItem ? searchData : data}
                columns={inboundDatacolumn}
                handlePageClick={(e) => {
                  // this.handlePagination(e);
                }}
                totalPage={totalPage}
                showBottomPagination={false}
              />
              <Pagination
                totalPage={totalPage}
                handleBack={this.handleBackPagination}
                handleNext={this.handleNextPagination}
                activePage={this.state.page ? this.state.page : 1}
                handlePagination={this.handlePagination}
              />
            </div>
          ) : null}
        </Card>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    CandidateReducer: state.CandidateReducer,
    JobDescriptionReducer: state.JobDescriptionReducer,
    resumesourceReducer: state.resumesourceReducer,
    responseReducer: state.responseReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    CandidateAction: bindActionCreators(Actions, dispatch),
    JDAction: bindActionCreators(JDActions, dispatch),
    resumesourceAction: bindActionCreators(CActions, dispatch),
    ResponseActions: bindActionCreators(ResponseActions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Selected);
